.App {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: .2rem 0;
  font-size: .16rem;

  > div.wrapper {
    display: flex;
    flex-flow: column;
    width: 60vw;
    margin-bottom: .6rem;
    padding: .1rem;
    border-radius: 8px;
    box-shadow: 0 0 5px #333;
    color: #ddd;
    background-color: #333;
    overflow: hidden;

    > main {
      flex: 1;
    }
  }
}
